.square {
  display: inline-block;
  border-style: solid;
  border-width: 1px;
  width: 50px;
  height: 50px;
  background-color: #37517e;
  color: black;
}

.square:hover {
  background-color: #2e4369;
}

.lighted {
  background-color: #00b9ff;
}

.lighted:hover {
  background-color: #00a4df;
}

.square:focus,
.lighted:focus {
  outline: none;
}
